import { Component } from '@angular/core';
import { TwitterSearchService } from 'src/api/twitter-search.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  constructor(private _twitterSearchService: TwitterSearchService) { }
  title = 'twitter-word-comparer';

  submit() {
    this._twitterSearchService.getTweetsByWord('poop').subscribe((data) => {
      let stop = true;
      console.log(data);
    }, error => console.log(error));
  }
}
